class BlogItem extends Component {
    render() {
        return (
            <div className='col-md-4 col-sm-6 portfolio-item'>
                <Link className='portfolio-link' to={`/blog/${this.props.post.slug}`}
                    onClick={e => this.props.setPostData(this.props.post)}
                >
                    <div className='portfolio-hover'>
                        <div className='portfolio-hover-content blog-item'>
                            <BookIcon className='blog-item-icon' />
                        </div>
                    </div>
                    {this.props.post.PostImage ?
                        this.props.post.PostImage.length > 0 ?
                            //<img style={{ width: '100%' }} className='img-fluid' src={API.makeFileURL(this.props.post.PostImage[0].thumbnail, null)} alt='' />
                            <img style={{ width: '100%' }} className='img-fluid' src={testImages[0]} alt='' />
                            : <img style={{ width: '100%' }} className='img-fluid' src={testImages[0]} alt='' />
                        : <img style={{ width: '100%' }} className='img-fluid' src={testImages[0]} alt='' />}
                </Link>
                <div className='portfolio-caption'>
                    <h4>{this.props.post.title}</h4>
                    <p className='text-muted'>{this.props.post.slug ? this.props.post.slug.length > 20 ? this.props.post.slug.substring(0, 18) + '...' : this.props.post.slug : null}</p>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state => ({

});

const mapDispatchToProps = dispatch => ({
    setPostData: (post) => {
        dispatch(SiteActions.setPostData(post));
    }
})
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(BlogItem);