import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withFormik, Form } from 'formik';
import { FormikTextField, FormikSelectField } from 'formik-material-fields';
import * as Yup from 'yup';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import { withRouter, Redirect } from 'react-router-dom';
import * as AdminActions from '../../../store/actions/adminActions';
const styles = theme => ({
    container: {
        margin: theme.spacing(3),
        display: 'flex',
        flexDirection: 'row wrap',
        width: '100%'
    },
    formControl: {
        margin: theme.spacing(0)
    },
    leftSide: {
        flex: 2,
        height: '100%',
        margin: theme.spacing(1),
        padding: theme.spacing(3)
    },
    rightSide: {
        flex: 1,
        height: '100%',
        margin: theme.spacing(1),
        padding: theme.spacing(3)
    },
    save: {
        marginBottom: theme.spacing(2)
    },
    postImage: {
        width: '100%'
    },
    halfWidth: {
        width: '50%'
    }
});
class AddUser extends Component {
    componentDidUpdate(props, state) {
        if (this.props.match.params.view === 'add' && this.props.admin.users.filter(p => p.name === this.props.values.name).length > 0) {
            const user = this.props.admin.users.filter(p => p.name === this.props.values.name)[0];
            this.props.history.push('/admin/users/edit/' + user.dispatch);


        }
        if (this.props.admin.user && this.props.admin.user.id !== props.admin.user.id) {
            //When redux state changes user in admin reducer
            this.props.setValues(this.props.admin.user)
        }
    }
    componentDidMount(props, state) {
        if (this.props.match.params.view === 'edit' && this.props.match.params.id) {
            this.props.getSingleUser(this.props.match.params.id, this.props.auth.token);
            //console.log('Id:' + this.props.match.params.id + ' token: ' + this.props.auth.token)
        }
    }
    render() {
        const { classes } = this.props;
        return (
            <Form className={classes.container}>
                <Paper className={classes.leftSide}>
                    <FormikTextField
                        name='name'
                        label='Name'
                        margin='normal'
                        fullWidth
                    />
                    <FormikTextField
                        name='email'
                        label='Email'
                        margin='normal'
                        fullWidth
                    />
                    <FormikTextField
                        name='password'
                        label='Password'
                        margin='normal'
                        type='password'
                        fullWidth
                    />
                    <FormikSelectField
                        name='role'
                        label='Role'
                        margin='normal'
                        options={[
                            { label: 'Admin', value: 0 },
                            { label: 'Subscriber', value: 1 },
                            { label: 'Editor', value: 2 }
                        ]}
                        className={classes.halfWidth}
                    />
                </Paper>
                <Paper className={classes.rightSide}>
                    <div className={classes.save}>
                        <Button
                            variant='contained'
                            color='secondary'
                            onClick={e => {
                                this.props.handleSubmit();
                                <Redirect to='/admin/users' />
                            }
                            }
                        ><SaveIcon /> Save</Button>
                    </div>
                </Paper>
            </Form>
        )
    }
}
const mapStateToProps = state => ({
    admin: state.admin,
    auth: state.auth
});
const mapDispatchToProps = dispatch => ({
    adminRegister: (user, token) => {
        dispatch(AdminActions.adminRegister(user, token));
    },
    updateUser: (user, token) => {
        dispatch(AdminActions.updateUser(user, token));
    },
    getSingleUser: (userId, token) => {
        dispatch(AdminActions.getSingleUser(userId, token));
    }
});

export default withRouter(connect(
    mapStateToProps,
    mapDispatchToProps
)(withFormik({
    mapPropsToValues: (props) => ({
        name: props.admin.user ? props.admin.user.name || '' : '',
        email: props.admin.user ? props.admin.user.email || '' : '',
        password: '',
        role: 1
    }),
    validationSchema: Yup.object().shape({
        name: Yup.string().required('Title is required'),
        email: Yup.string().email().required('Email is required'),
        password: Yup.string().required('Password is required')
    }),
    handleSubmit: (values, { setSubmitting, props }) => {
        console.log(props.auth.token);
        if (props.match.params.view === 'edit') {
            const user = {
                ...values,
                id: props.match.params.id
            }
            props.updateUser(user, props.auth.token)
        } else {
            props.adminRegister(values, props.auth.token);
        }

    }
})(withStyles(styles)(AddUser))));