import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import * as AdminActions from '../../../store/actions/adminActions';

import UserIcon from '../../assets/img/user.png';
import '../../assets/css/admin.css';
class Dashboard extends Component {
    componentDidMount() {
        this.props.getPosts(this.props.auth.token);
        this.props.getUsers(this.props.auth.token);
        this.props.getComments(this.props.auth.token);
    }
    render() {
        return (
            <div className='p100'>
                <div className='admin-title'>Dashboard</div>
                <Divider />
                <div className='dashboard-main-row'>
                    <div className='admin-row'>
                        <Paper className='admin-tale'>
                            <div className='dashboard-counter'>{this.props.admin.users ? this.props.admin.users.length : null}</div>
                            Users
                        </Paper>
                        <Paper className='admin-tale'>
                            <div className='dashboard-counter'>{this.props.admin.posts ? this.props.admin.posts.length : null}</div>
                            Posts
                        </Paper>
                        <Paper className='admin-tale'>
                            <div className='dashboard-counter'>{this.props.admin.comments ? this.props.admin.comments.length : null}</div>
                            Comments
                        </Paper>

                    </div>
                    <Paper className='admin-user-menu'>
                        <img className='avatar' src={UserIcon} />
                        <div className='text-1'>{this.props.auth.user.name}</div>
                        <li className='admin-divider'><Divider /></li>
                        <div className='text-3'>{this.props.auth.user.Profile.role === 'admin' ? 'Administrator' : this.props.auth.user.Profile.role === 'editor' ? 'Editor' : this.props.auth.user.Profile.role === 'subscriber' ? 'Subscriber' : ''}</div>
                        <li className='admin-divider'><Divider /></li>
                        <div className='text-2'>
                            Email: {this.props.auth.user.email}
                        </div>
                        <div className='text-2'>
                            Since: {this.props.auth.user.Profile ? this.props.auth.user.Profile.created_at ? this.props.auth.user.Profile.created_at.toString().substring(0, this.props.auth.user.Profile.created_at.toString().length - 14) : null : null}
                        </div>
                    </Paper>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state => ({
    auth: state.auth,
    admin: state.admin
})
const mapDispatchToProps = dispatch => ({
    getUsers: (token) => {
        dispatch(AdminActions.getUsers(token));
    },
    getPosts: (token) => {
        dispatch(AdminActions.getPosts(token));
    },
    getComments: (token) => {
        dispatch(AdminActions.getComments(token));
    }
})
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Dashboard));