import React, { Component } from 'react';
import { connect } from 'react-redux';
import Header from '../common/header';
import API from '../../utils/api';
import * as SiteActions from '../../store/actions/siteActions';
import { Link } from 'react-router-dom';
import CommentBuilder from '../common/commentbuilder';
import Divider from '@material-ui/core/Divider';
import Paper from '@material-ui/core/Paper';

import DeleteIcon from '@material-ui/icons/Delete';
import UserIcon from '../assets/img/user.png';

import '../../components/assets/css/blog.css';

const testImages = [
    'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/5d608093-250f-4039-967f-ea8b3a36c56f/ddixu1p-0e3d1ae9-5ccc-4c38-a875-7eb39031c6fc.png?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJ1cm46YXBwOiIsImlzcyI6InVybjphcHA6Iiwib2JqIjpbW3sicGF0aCI6IlwvZlwvNWQ2MDgwOTMtMjUwZi00MDM5LTk2N2YtZWE4YjNhMzZjNTZmXC9kZGl4dTFwLTBlM2QxYWU5LTVjY2MtNGMzOC1hODc1LTdlYjM5MDMxYzZmYy5wbmcifV1dLCJhdWQiOlsidXJuOnNlcnZpY2U6ZmlsZS5kb3dubG9hZCJdfQ.bgsGt9hIDTeqyJN-aaMC__i2OKaOBteo64k2MpI3hJU',
    'https://media.pocketgamer.com/artwork/na-qwggnas/image.jpeg',
    'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/06373d2c-2485-4865-ab62-77409cc10433/ddaq65o-37dba794-ee4f-4a6f-8c59-5fe40bfc0e1a.png?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJ1cm46YXBwOiIsImlzcyI6InVybjphcHA6Iiwib2JqIjpbW3sicGF0aCI6IlwvZlwvMDYzNzNkMmMtMjQ4NS00ODY1LWFiNjItNzc0MDljYzEwNDMzXC9kZGFxNjVvLTM3ZGJhNzk0LWVlNGYtNGE2Zi04YzU5LTVmZTQwYmZjMGUxYS5wbmcifV1dLCJhdWQiOlsidXJuOnNlcnZpY2U6ZmlsZS5kb3dubG9hZCJdfQ.oBkAnnIErWKlHwiuJw_p9zzRQPsPWzdn8wf_U_7Vm3k',
    'https://cdn6.aptoide.com/imgs/7/3/9/739c6c9cc9ee35022efca0c1f2628656_icon.png',
    'https://image.flaticon.com/icons/png/512/1297/1297826.png'
]
class Single extends Component {
    componentDidMount() {
        this.props.getSinglePost(this.props.match.params.slug, this.props.auth.token);
    }
    componentDidUpdate() {

        //this.props.getSinglePost(this.props.match.params.slug, this.props.auth.token);
    }
    render() {
        return (
            <div className='blogPost'>
                <Header
                    title=''
                    subtitle={this.props.site.post.title}
                    link='/services'
                    showButton={false}
                    image={
                        this.props.site.post.PostImage ?
                            this.props.site.post.PostImage.length > 0 ?
                                //API.makeFileURL(this.props.site.post.PostImage[0].url, this.props.auth.token) 
                                testImages[0]
                                : testImages[0] : testImages[0]}
                />
                <div className='container content'>

                    <div className='row content'>

                        <div className='col-md-9'>
                            <div className='date-text-row'>
                                <div className='date-text'>Created: {this.props.site.post.createdAt} </div>
                                <div className='date-text'>{this.props.site.post.modified ? 'Modified: ' + this.props.site.post.modified : ''}</div>
                            </div>
                            <li className='divider'><Divider /></li>
                            <div className='post-content' dangerouslySetInnerHTML={{ __html: this.props.site.post.content }}></div>
                        </div>
                    </div>

                    <Paper className='row comment-section'>
                        <div className='col-md-12 comment-builder'>
                            <h3>Comments</h3>
                            {this.props.auth.token ?
                                <CommentBuilder />
                                : <p>Want to comment? <Link to='/signup'>Sign Up!</Link></p>}
                        </div>

                        <div className='row comment'>
                            {this.props.site.post.Comments ?
                                this.props.site.post.Comments.length > 0 ?
                                    this.props.site.post.Comments.map((comment, i) => {
                                        return (
                                            <Paper key={i} className='comment-row'>
                                                <div className='start'>
                                                    <img className='user-icon' src={UserIcon} />
                                                    <div className='comment-column'>
                                                        <h5>{comment.Profile ? comment.Profile.name == null ? 'Unknown' : comment.Profile.name : ''}</h5>
                                                        <p>{comment.content}</p>

                                                    </div>
                                                </div>
                                                <div className='end'>
                                                    <div className='comment-date'>{comment.createdAt}</div>
                                                    {this.props.auth.user.id === comment.userId ?
                                                        <DeleteIcon onClick={e => { this.props.deleteComment(comment.id, this.props.site.post.slug, this.props.auth.token) }} className='comment-icon' /> : <div className='empty-icon'></div>
                                                    }
                                                </div>
                                            </Paper>
                                        )
                                    }) : null : 'There is no Comments'}
                        </div>
                    </Paper>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => ({
    auth: state.auth,
    site: state.site
});
const mapDispatchToProps = dispatch => ({
    getSinglePost: (slug, token) => {
        dispatch(SiteActions.getPostBySlug(slug, token));
    },
    deleteComment: (commentId, slug, token) => {
        dispatch(SiteActions.deleteComment(commentId, slug, token));
    }
});
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Single);