import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../assets/css/blog.css';
class Header extends Component {
    render() {
        return (
            <header className='masthead' style={{ backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(${this.props.image})`, backgroundPosition: 'center', backgroundSize: 'cover', display: 'fex', alignItems: 'center' }}>
                <div className='container'>
                    <div className='intro-text'>
                        <div className='intro-lead-in'>{this.props.title}</div>
                        <div className={`intro-heading text-uppercase outline`} >{this.props.subtitle}</div>
                        {this.props.showButton && <Link className='btn btn-primary btn-xl text-uppercase js-scroll-trigger' to={this.props.link}>{this.props.buttonText}</Link>}
                    </div>
                </div>
            </header>
        )
    }
}
export default Header;