import React, { Component } from 'react';
import TableView from '../../common/TableView';
import { connect } from 'react-redux';
import * as AdminActions from '../../../store/actions/adminActions';
import Fab from '@material-ui/core/Fab';
import EditIcon from '@material-ui/icons/Edit';
import { Link as RouterLink } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';

const cols = [
    { label: 'ID', name: 'id' },
    { label: 'Email', name: 'email' },
    { label: 'Name', name: 'name' }
]
const styles = theme => ({
    fab: {
        position: 'fixed',
        bottom: '50px',
        right: '50px'
    }
});
class Users extends Component {
    componentDidMount() {
        // this.props.getUsers(this.props.auth.token);
    }
    render() {
        const { classes } = this.props;
        const users = this.props.admin.users;
        return (
            <div>
                <h1>Users</h1>
                <TableView
                    columns={cols}
                    rows={users}
                    path={'/admin/users'}
                />
                <Fab component={RouterLink} to='/admin/users/add' color='secondary' aria-label='Add' className={classes.fab}>
                    <EditIcon />
                </Fab>
            </div>
        )
    }
}
const mapStateToProps = state => ({
    auth: state.auth,
    admin: state.admin
})
const mapDispatchToProps = dispatch => ({
    getUsers: (token) => {
        dispatch(AdminActions.getUsers(token))
    }
})
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Users));