import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import './App.css';
//Pages
import Login from './components/Pages/Login';
import Blog from './components/Pages/Blog';
import Single from './components/Pages/Single';
import Signup from './components/Pages/Signup';
//Admin Pages
import Dashboard from './components/Pages/Admin/Dashboard';
import Users from './components/Pages/Admin/Users';
import Posts from './components/Pages/Admin/Posts';
import AddPost from './components/Pages/Admin/AddPost';
import AddUser from './components/Pages/Admin/AddUser';

import AdminWrapper from './components/AdminWrapper';
import LoginWrapper from './components/LoginWrapper';
class App extends Component {
  render() {
    return (
      <Router>
        <Route
          exact={true}
          path='/admin/users/:view/:id'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  this.props.auth.user.Profile ?
                    this.props.auth.user.Profile.role === 'admin' ?
                      <AdminWrapper>
                        <AddUser />
                      </AdminWrapper>
                      : <Redirect to='/' />
                    : null
                  : <LoginWrapper>
                    <Login />
                  </LoginWrapper>
                }
              </div>
            )
          }}
        />
        <Route
          exact={true}
          path='/admin/users/:view'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  this.props.auth.user.Profile ?
                    this.props.auth.user.Profile.role === 'admin' ?
                      <AdminWrapper>
                        <AddUser />
                      </AdminWrapper>
                      : <Redirect to='/' />
                    : null
                  : <LoginWrapper>
                    <Login />
                  </LoginWrapper>
                }
              </div>
            )
          }}
        />
        <Route
          exact={true}
          path='/admin/users'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  this.props.auth.user.Profile ?
                    this.props.auth.user.Profile.role === 'admin' ?
                      <AdminWrapper>
                        <Users />
                      </AdminWrapper>
                      : <Redirect to='/' />
                    : null
                  : <LoginWrapper>
                    <Login />
                  </LoginWrapper>
                }
              </div>
            )
          }}
        />
        <Route
          exact={true}
          path='/admin/posts/:view/:id'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  this.props.auth.user.Profile ?
                    this.props.auth.user.Profile.role === 'admin' ?
                      <AdminWrapper>
                        <AddPost />
                      </AdminWrapper>
                      : <Redirect to='/' />
                    : null
                  : <LoginWrapper>
                    <Login />
                  </LoginWrapper>
                }
              </div>
            )
          }}

        />
        <Route
          exact={true}
          path='/admin/posts/:view'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  this.props.auth.user.Profile ?
                    this.props.auth.user.Profile.role === 'admin' ?
                      <AdminWrapper>
                        <AddPost />
                      </AdminWrapper>
                      : <Redirect to='/' />
                    : null
                  : <LoginWrapper>
                    <Login />
                  </LoginWrapper>
                }
              </div>
            )
          }}

        />
        <Route
          exact={true}
          path='/admin/posts'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  this.props.auth.user.Profile ?
                    this.props.auth.user.Profile.role === 'admin' ?
                      <AdminWrapper>
                        <Posts />
                      </AdminWrapper>
                      : <Redirect to='/' />
                    : null
                  : <LoginWrapper>
                    <Login />
                  </LoginWrapper>
                }
              </div>
            )
          }}
        />
        <Route
          exact={true}
          path='/signup'
          render={props => {
            if (this.props.auth.token) {
              return (
                <Redirect to='/' />
              )
            } else {
              return (
                <LoginWrapper>
                  <Signup />
                </LoginWrapper>
              )
            }
          }} />
        <Route
          exact={true}
          path='/admin'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  this.props.auth.user.Profile ?
                    this.props.auth.user.Profile.role === 'admin' ?
                      <AdminWrapper>
                        <Dashboard />
                      </AdminWrapper>
                      : <Redirect to='/' />
                    : null
                  : <LoginWrapper>
                    <Login />
                  </LoginWrapper>
                }
              </div>
            )
          }}
        />
        <Route
          exact={true}
          path='/login'
          render={props => {
            return (
              <div>
                {this.props.auth.token ?
                  <Redirect to='/' />
                  :
                  <LoginWrapper>
                    <Login />
                  </LoginWrapper>

                }
              </div>
            )
          }}
        />
        <Route
          exact={true}
          path='/'
          render={props => (
            <Redirect to='/blog' />
          )}
        />
        <Route
          exact={true}
          path='/blog/:slug'
          render={props => (
            <Single {...props} />
          )}
        />
        <Route
          exact={true}
          path='/blog'
          render={props => (
            <Blog {...props} />
          )}
        />
      </Router>
    );
  }
}
const mapStateToProps = state => {
  return {
    auth: state.auth
  }
}
const mapDispatchToProps = dispatch => {
  return {

  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
